@import "../../../utilities/config";

$container-color: #18181b;
$container-active-color: $container-color;
$container-color--transparent: #18181bc5;

.users-container {
  height: $section-height;
  overflow: hidden;

  display: flex;
  flex-direction: column;

  background: $container-color;
}

.users--header {
  display: flex;
  justify-content: space-between;
}

.users--main {
  display: grid;
  overflow-y: scroll;

  height: 100%;
  align-content: flex-start;

  grid-template-columns: repeat(auto-fit, minmax($min-block-width, 1fr));

  @include --moz-color-scrollbar(#717174, $container-color--transparent);
  @include --web-color-scrollbar(#717174, $container-color--transparent);
}

.one-card-container {
  // grid-template-columns: 1fr;
  // grid-template-rows: 1fr;
  align-items: center;
  justify-content: center;
  
  .user {
    transition: .2s linear;
    width: 50%;
    margin: 2% auto 0;
    transform: scale(1.05);
  }
}

.droppable--inactive {
  background: $container-active-color;

  $lines-color: rgb(110, 0, 0);
  background: repeating-linear-gradient(
    45deg,
    $lines-color,
    $lines-color 17px,
    #18181b 17px,
    #18181b 50px
  );
}
