@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body{background:#2e3138}

header{padding-left:1%;color:white;font-size:1.5rem;font-family:'Roboto', sans-serif}

.main-container{display:grid;overflow:hidden;padding:.5vw;grid-gap:.5vw;gap:.5vw;grid-template-columns:minmax(345px, 2fr) minmax(230px, 1fr) minmax(230px, 1fr);grid-template-rows:auto}@media only screen and (max-width: 820px){.main-container{grid-template-columns:minmax(345px, 2fr) 0 minmax(230px, 1fr)}}@media only screen and (max-width: 590px){.main-container{grid-template-columns:minmax(345px, 2fr) 0 0}}

.users-container{height:90vh;overflow:hidden;display:flex;flex-direction:column;background:#18181b}.users--header{display:flex;justify-content:space-between}.users--main{display:grid;overflow-y:scroll;height:100%;align-content:flex-start;grid-template-columns:repeat(auto-fit, minmax(230px, 1fr));scrollbar-color:#717174 rgba(24,24,27,0.772549)}.users--main::-webkit-scrollbar{width:1vw}.users--main::-webkit-scrollbar-track{background:rgba(24,24,27,0.772549)}.users--main::-webkit-scrollbar-thumb{background-color:#717174;border-radius:1px}.users--main::-webkit-scrollbar-button{height:3px;background:rgba(24,24,27,0.772549)}.one-card-container{align-items:center;justify-content:center}.one-card-container .user{transition:.2s linear;width:50%;margin:2% auto 0;transform:scale(1.05)}.droppable--inactive{background:#18181b;background:repeating-linear-gradient(45deg, #6e0000, #6e0000 17px, #18181b 17px, #18181b 50px)}

.search-users-input{min-width:225px;height:1.5rem;margin:5px;padding:18px;padding-left:15px;border-radius:4px;font-size:0.9rem;font-family:Arial, Helvetica, sans-serif;border:white;outline:none;color:#cccccc;background:#23252b}.search-users-input:focus{color:white}

.room-switches-container{width:100%;overflow:auto;display:flex;flex-flow:row wrap;align-items:center;justify-content:flex-end;grid-row-gap:3px;row-gap:3px;grid-column-gap:3px;-webkit-column-gap:3px;column-gap:3px;margin:5px 10px 2px 10px}.room-switch{width:35px;height:35px;border-radius:10%;background-color:#3f3f3f;color:white;display:flex;align-items:center;justify-content:center;font-size:1em;font-family:"Arial", sans-serif;transition:0.5s linear}.room-switch:hover{position:relative;background-color:gray;cursor:pointer;transition:0.3s linear}.room-switch:active{transition:0.5s linear}.active-room{box-sizing:border-box;background:rgba(0,212,255,0.5)}

.user{height:100px;display:flex;align-items:center;justify-content:space-between;color:#fff;margin:5px;border-radius:3px;overflow:hidden;transition:.3s ease-in;box-sizing:border-box;background:#3a3a3d;cursor:-webkit-grab;cursor:grab}.active-user:hover{transition:.3s ease-in;background:#111160}.user img{width:70px;height:70px;display:inline-block;overflow:hidden;color:#0000;margin:0 10px;border-radius:100%;background:#23252b}.banned-status{border-top:5px solid red}.unbanned-status{border-top:5px solid green}

.activeUsers-container{max-height:90vh;overflow-y:scroll;background:rgba(24,24,27,0.313725);scrollbar-color:#717174 rgba(24,24,27,0.313725)}.activeUsers-container::-webkit-scrollbar{width:1vw}.activeUsers-container::-webkit-scrollbar-track{background:rgba(24,24,27,0.313725)}.activeUsers-container::-webkit-scrollbar-thumb{background-color:#717174;border-radius:1px}.activeUsers-container::-webkit-scrollbar-button{height:3px;background:rgba(24,24,27,0.313725)}.droppable--active{opacity:.7}

.actions-container{display:grid;height:90vh;grid-template-rows:1fr auto;grid-row-gap:0.5vw;row-gap:0.5vw}

.server-actions-container{display:grid;height:100%;overflow:auto;min-height:30vh;grid-template-rows:repeat(auto-fit, minmax(1fr));grid-gap:1vh;background:#18181baa}

.server-action{display:flex;align-items:center;justify-content:center;position:relative;border-radius:1%;background:#3a3a3d;color:white;font-size:4vh;font-family:"Russo One", sans-serif;overflow:hidden;transition:color .5s ease}.server-action span{z-index:2}.server-action:active{transition:0.3s linear;background:rgba(255,255,255,0.705)}.server-action:hover{transition:color .5s ease;cursor:pointer;color:gold}.server-action::before{content:"";position:absolute;width:0%;height:100%;background:rgba(255,255,255,0.705);transition:all 0.75s ease-in}.page-action-click-animation::before{width:100%;height:100%;opacity:1;transition:all 0.5s linear backwards;width:100%;height:100%;opacity:1;background:rgba(7,202,192,0.705);transition:all 0.2s linear}

.page-actions-container{display:grid;grid-gap:1vw;padding:5%;grid-template-rows:repeat(2, minmax(10vh, 10vw));grid-template-columns:repeat(2, minmax(10vh, 10vw));align-content:center;justify-content:center;background:#18181baa}

.page-action{display:flex;align-items:center;justify-content:center;padding:10px;border-radius:20%;background:#3a3a3d;position:relative;overflow:hidden;transition:0.3s linear}.page-action svg{opacity:1;border-radius:inherit;box-sizing:border-box}.page-action:active{transition:0.3s linear;background:rgba(255,255,255,0.705)}.page-action:hover{cursor:pointer}svg.page-action--rotate:hover{-webkit-animation:rotate 2.1s ease infinite;animation:rotate 2.1s ease infinite}@-webkit-keyframes rotate{to{transform:rotate(360deg)}}@keyframes rotate{to{transform:rotate(360deg)}}svg.page-action--shaking:hover{-webkit-animation:shaking 0.25s ease infinite;animation:shaking 0.25s ease infinite}@-webkit-keyframes shaking{0%{transform:rotate(0deg) translateY(0%)}25%{transform:rotate(10deg) translateY(1%)}75%{transform:rotate(-10deg) translateY(-1%)}100%{transform:rotate(0deg) translateY(0%)}}@keyframes shaking{0%{transform:rotate(0deg) translateY(0%)}25%{transform:rotate(10deg) translateY(1%)}75%{transform:rotate(-10deg) translateY(-1%)}100%{transform:rotate(0deg) translateY(0%)}}svg.page-action--alien{-webkit-animation:dissapear 1s linear;animation:dissapear 1s linear}svg.page-action--alien #tree{-webkit-animation:moveUp 1.1s ease-in;animation:moveUp 1.1s ease-in}svg.page-action--alien:hover{-webkit-animation:shaking 2s ease infinite;animation:shaking 2s ease infinite}svg.page-action--alien:hover #tree{-webkit-animation:moveUp 1.1s ease-in infinite;animation:moveUp 1.1s ease-in infinite}@-webkit-keyframes dissapear{0%{opacity:0}100%{opacity:1}}@keyframes dissapear{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes moveUp{from{transform:translateY(50%)}to{transform:translateY(-50%)}}@keyframes moveUp{from{transform:translateY(50%)}to{transform:translateY(-50%)}}@keyframes shaking{0%{transform:rotate(0deg)}50%{transform:rotate(5deg)}100%{transform:rotate(0deg)}}svg.page-action--diskette{transform:scale(0.9);transition:.25s linear}svg.page-action--diskette #gate{transition:.25s linear}svg.page-action--diskette:hover{transition:.25s linear;transform:translateX(3px)}svg.page-action--diskette:hover #gate{transition:.25s linear;transform:translateX(20px)}svg.page-action--diskette #left--save{transition:1s ease}svg.page-action--diskette #left--save:hover{fill:white}svg.page-action--diskette #right--load{transition:1s ease}svg.page-action--diskette #right--load:hover{fill:black}


